import React, { Component } from "react";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { createNumberMask } from "redux-form-input-masks";
import { getTimbangan } from "../../GetTimbangan";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../helper";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormPengambilanTimbangan extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
    };
  }

  componentDidMount() {
    AxiosMasterGet("jenis/get/by-kode-kelompok/PERHIASAN")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("pkg", this.props.pkg);
  }

  pilihBarang(data) {
    var supp = data ? data : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarJual: result_explode[3],
      kadarBeli: result_explode[4],
      barang: "terpilih",
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 form-group">
            {String(this.state.kodeBarang) !== "" ? (
              <Field
                label="Kode Barang"
                name="kodeJenis"
                component={renderFieldSelect}
                options={this.state.kodeBarang.map((list) => {
                  let data = {
                    value:
                      list.kode_jenis +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_jual +
                      "|" +
                      list.kadar_beli,
                    name: list.nama_jenis,
                  };
                  return data;
                })}
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                placeholder="Silahkan Pilih"
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Barang" />
            )}
          </div>
          <div className="col-md-8 form-group">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>

          <div className="col-md-3 form-group">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          {/* <div className="col-md-3 form-group">
            <Field name="p" label="P" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field name="k" label="K" type="number" component={renderField} />
          </div> */}
          <div className="col-md-3 form-group">
            <Field
              name="pkg"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
              readOnly
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadarTransaksi"
              label="Harga (%)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="gross"
              label="Gross (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="netto"
              label="Murni (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormPengambilanTimbangan = reduxForm({
  form: "TerimaSupplier",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormPengambilanTimbangan);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, gross } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "gross"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    pkg: (parseFloat(gross) - parseFloat(bruto)).toFixed(3),
  };
})(FormPengambilanTimbangan);
