import React, { Component } from "react";
import SelectSearch from "react-select-search";
import Swal from "sweetalert2";
import { getListDataPengambilanBarcode } from "../actions/pengambilanBarcodeAction";
import { connect } from "react-redux";
import { reset } from "redux-form";
import HeaderCard from "../HeaderCard";
import { getUUID } from "../actions/fakturAction";
import { getcustomerList } from "../actions/customerAction";
import { getLokasiPindah, getsupplierList } from "../actions/supplierAction";
import { AxiosMasterPost } from "../Axios";
import { berhasil, gagal, warning } from "../Alert";
import { multipleDeleteLocal } from "../helper";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { finishSend, progressSend } from "../actions/LogicAction";
import FormPengambilanTimbangan from "./Component/FormPengambilanTimbangan";

const maptostate = (state) => {
  return {
    pengambilanBarcode: state.provinsi.pengambilanBarcode,
    noFaktur: state.provinsi.noFaktur,
    customer: state.provinsi.customer,
    supplier: state.provinsi.supplier,
    lokasi: state.provinsi.dataLokasiPindah,
    onSend: state.provinsi.onSend,
  };
};
class TransaksiTimbanganBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenisTransaksi: [
        {
          value: "RETUR SUPPLIER",
          name: "RETUR SUPPLIER",
        },
        {
          value: "PENERIMAAN SUPPLIER",
          name: "PENERIMAAN SUPPLIER",
        },
        {
          value: "PINDAH BARANG",
          name: "PINDAH BARANG",
        },
        {
          value: "PENJUALAN",
          name: "PENJUALAN",
        },
        {
          value: "RETUR CUSTOMER",
          name: "RETUR CUSTOMER",
        },
      ],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kode_barcode",
          text: "Barcode",
        },
        {
          dataField: "gross",
          text: "Gross",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
        },
        {
          dataField: "kadar",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos(RP)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, index) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_4"
                  onClick={() => this.hapus_barcode(index)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
      kode_supplier: "",
      kode_customer: "",
      kode_lokasi_asal: "",
      kode_lokasi_tujuan: "",
      tanggal: "",
    };
  }

  hapus_barcode(index) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    })
      .then((result) => {
        if (result.isConfirmed) {
          let data = JSON.parse(
            localStorage.getItem("pengambilanBarcode_temp")
          );
          data.splice(index, 1);
          localStorage.setItem("pengambilanBarcode_temp", JSON.stringify(data));
        }
      })
      .then(() => this.props.dispatch(getListDataPengambilanBarcode()));
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    this.props.dispatch(getListDataPengambilanBarcode());
    this.props.dispatch(getUUID());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getLokasiPindah());
  }
  paddingNumberGW(str) {
    var pad = "0000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  paddingNumberNW(str) {
    var pad = "00000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      kode_jenis: split[0],
      nama_jenis: split[1],
      kadar_cetak: split[2],
      kadar: split[3],
      pkg: data.pkg,
      gross: data.gross,
      bruto: data.bruto,
      netto: data.netto,
      ongkos_rp: data.rp || 0,
    };
    var array =
      JSON.parse(localStorage.getItem("pengambilanBarcode_temp")) || [];
    array.push(simpan);
    localStorage.setItem("pengambilanBarcode_temp", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListDataPengambilanBarcode());
    this.props.dispatch(reset("FormPengambilanBarcode"));
    document.getElementById("dismissModal").click();
  }

  pengambilanBarcodeSubmit() {
    if (this.state.kode_customer === "") {
      warning("Mohon Isi Nama Toko");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon Isi Tanggal");
      return false;
    }
    var instansi = this.state.kode_customer;
    var instansi2 = this.state.kode_customer;
    var local = JSON.parse(localStorage.getItem("pengambilanBarcode_temp"));
    var qty = local.length;
    var bruto = local
      .map((list) => parseFloat(list.bruto))
      .reduce((a, b) => a + b, 0);
    var pkg = local
      .map((list) => parseFloat(list.pkg))
      .reduce((a, b) => a + b, 0);
    var gross = local
      .map((list) => parseFloat(list.gross))
      .reduce((a, b) => a + b, 0);
    var netto = local
      .map((list) => parseFloat(list.netto))
      .reduce((a, b) => a + b, 0);
    var ongkos_rp = local
      .map((list) => parseFloat(list.ongkos_rp))
      .reduce((a, b) => a + b, 0);
    this.props.dispatch(progressSend());
    var simpan = {
      no_timbang: this.props.noFaktur,
      jenis_trx: "PENJUALAN",
      tanggal: this.state.tanggal,
      baki: "-",
      instansi: instansi,
      instansi2: instansi2,
      total_qty: qty,
      total_bruto: bruto,
      total_pkg: pkg,
      total_gross: gross,
      total_netto: netto,
      total_ongkos: ongkos_rp || 0,
      detail_barang: JSON.parse(
        localStorage.getItem("pengambilanBarcode_temp")
      ),
    };
    AxiosMasterPost("pengambilan-timbang/simpan", simpan)
      .then(() => berhasil("Simpan Data Berhaisl"))
      .then(() => multipleDeleteLocal(["pengambilanBarcode_temp", "noFaktur"]))
      .then(() => this.props.dispatch(getListDataPengambilanBarcode()))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(finishSend()))
      .then(() => window.location.reload())
      .catch((err) =>
        gagal(`Gagal Simpan Data , Detail : ${err.response.data}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  // setJenisTransaksi(e) {
  //   this.setState({
  //     jenisTransaksiSelected: e,
  //   });
  //   localStorage.setItem("jenisTransaksi", e);
  // }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Timbang Barang" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    {/* <div className="col-lg-4">
                      <Field
                        name="jenisTransaksi"
                        component={renderFieldSelect}
                        options={this.state.jenisTransaksi}
                        type="text"
                        label="Jenis Transaksi"
                        placeholder="Masukan Jenis Transaksi"
                        onChange={(data) => this.setJenisTransaksi(data)}
                      />
                    </div> */}
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                        onChange={this.inputChange("tanggal")}
                      />
                    </div>
                    {/* <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Baki</label>
                      <input
                        type="text"
                        id="baki"
                        name="baki"
                        className="form-control"
                        onChange={this.inputChange("baki")}
                      />
                    </div> */}
                    <div className="col-md-5 col-lg-4">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={this.props.customer.map((list) => {
                          let data = {
                            value: list.kode_customer,
                            name: list.nama_customer,
                          };
                          return data;
                        })}
                        search
                        label="Nama Toko"
                        placeholder="Silahkan Pilih"
                        onChange={(data) =>
                          this.setState({
                            kode_customer: data,
                          })
                        }
                      />
                    </div>
                    <div className="col-lg-12 col-md-12 mt-4">
                      <div className="row">
                        <div className="col-lg-6"></div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              data-target="#primary-header-modal"
                            >
                              TAMBAH DATA
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="kodeJenis"
                        data={this.props.pengambilanBarcode || []}
                        columns={this.state.columns}
                        textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.pengambilanBarcodeSubmit()}
                          disabled={this.props.onSend}
                        >
                          SIMPAN
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Pengambilan Timbangan
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormPengambilanTimbangan
                  onSubmit={(data) => this.handleSubmit(data)}
                  noFaktur={this.props.noFaktur}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TransaksiTimbanganBarang);
